.emailRow {
  display: flex;
  align-items: center;
  height: 50px;
  border-bottom: 1px solid whitesmoke;
  cursor: pointer;
  z-index: 999;

}

.emailRow:hover {
  border-top: 1px solid whitesmoke;
  box-shadow: 8px 4px 4px -2px rgba(0, 0, 0, 0.24);
}

.emailRow_options {
  display: flex;
}

.emailRow_message {
  display: flex;
  flex: 0.8;
  align-items: center;
  font-style: 13px;
}
.emailRow_message > h4 {
  width: 400px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 5px;
  padding-right: 5px;
}

.emailRow_title {
  padding-right: 13px;
  flex: 0.3;
}

.emailRow_time {
  padding-right: 15px;
  font-style: 9px;
  font-weight: bold;
}

.emailRow_description {
  font-weight: 400;
  color: gray;
}
