.sendMail {
  position: absolute;
  bottom: 0;
  right: 50px;
  background-color: #404040;
  width: 40%;
  height: 40%;
  min-width: 500px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  flex-direction: column;
  border: 1px solid whitesmoke;
  box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.24);
}

.sendMail_header {
  padding: 13px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: gray;
}
.sendMail_header > h3 {
  color: whitesmoke;
  font-size: 13px;
}
.sendMail_send {
  background-color: #3879ed !important;
  text-transform: capitalize !important;
  margin: 15px !important;
  color: black;
}

.sendMail_close {
  cursor: pointer;
}

.sendMail > form {
  display: flex;
  flex: 1;
  flex-direction: column;
}
.sendMail > form > input {
  height: 30px;
  padding: 10px;
  bottom: none;
  border-radius: 1px solid whitesmoke;
  outline: none;
}

.sendMail_message {
  flex: 1;
}

.sendMail_error {
  background-color: #fff;
  color: red;
  text-align: right;
  padding: 2px;
}
